export class ModalModel {
    constructor(value, title, body) {
        if (title) {
            this.title = title
        } else {
            this.title = ''
        }
        if (body) {
            this.body = body
        } else {
            this.body = ''
        }
        this.title = title
        this.name = body
        this.value = value;
    }
}